<template>
    <div id="detail">
        <p id="title">
            北师大经三千多病例发现，女性或无免于感染新型肺炎优势
        </p>
        <div id="news-msg">
            <div><img src="https://p3.pstatp.com/thumb/8532/7581013616">{{source}}</div>
            <div>17小时前 评论</div>
        </div>
        <div id="content" v-html="content">
        </div>
    </div>
</template>

<script>
export default {
    name: 'Detail',
    props: {
        newsId: String
    },
    data(){
        return{
            content: `<div class="pgc-img"><img src="https://p3.pstatp.com/large/pgc-image/RpixriJ5CGUflY" img_width="924" img_height="577" inline="0" alt="北师大经三千多病例发现，女性或无免于感染新型肺炎优势" onerror="javascript:errorimg.call(this);"><p class="pgc-img-caption"></p></div><p>图片来源：新京报网</p><p>文 | 吴晔 李明会 樊佳</p><p>疫情当下，“宅”成为生活必需，手机不断被拿起，放下，拿起，放下……数亿人坐立难安，生活在信息刷屏的焦虑中。</p><p>我们通过官方发布的公开信息，采集到3475个病例信息。在被疫情信息刷屏的情况下，我们用数据进行分析，发现病例背后的隐藏信息，让理性赶走焦虑。</p><p><strong>女性或不存在免于感染的性别优势</strong></p><div class="pgc-img"><img src="https://p3.pstatp.com/large/pgc-image/RpixrixEl9AF42" img_width="1592" img_height="1242" inline="0" alt="北师大经三千多病例发现，女性或无免于感染新型肺炎优势" onerror="javascript:errorimg.call(this);"><p class="pgc-img-caption"></p></div><p>图1 研究样本性别比例</p><p>3475个带有性别的信息中，除去82个缺失，男性感染者人数多于女性。其中，男性1843例，女性1550例。虽然在早先的病例研究中，男性患者远多于女性。但截至目前，我们看到女性感染者越来越多，似乎要打破先前研究者所得出的“男性或比女性更易感染新型冠状病毒”的论断。</p><p>从1月11日至1月15日，武汉新型冠状病毒肺炎人数曾一直停留在41人。武汉金银潭医院黄朝林等人研究了这41个病例的临床特征。其中，30例为男性，占73%。</p><p>武汉金银潭医院张丽教授和上海瑞金医院张欣欣教授以1月1日至20日期间武汉金银潭医院收治的99名新型冠状病毒感染者为样本进行研究。文章认为，女性的病毒易感性略低，可能是因为X染色体和性腺激素的保护，这二者在先天性和适应性免疫中起着重要作用。这一结论于1月29日发表在国际医学期刊《柳叶刀》上。</p><p><strong>35岁-39岁人群危险系数最高</strong></p><div class="pgc-img"><img src="https://p3.pstatp.com/large/pgc-image/Rpixrkz4LOeeci" img_width="1129" img_height="1326" inline="0" alt="北师大经三千多病例发现，女性或无免于感染新型肺炎优势" onerror="javascript:errorimg.call(this);"><p class="pgc-img-caption"></p></div><p>图2.1 研究样本年龄分布 图2.2研究样本各年龄段权重</p><p>在采集到的3475个案例中，25岁-59岁人群占75%，但在0-94岁不同年龄组中都有发病案例。这说明虽然每个人由于年龄原因，接触暴露的机会不同，发病率不同，但是新型冠状病毒对所有年龄组而言都是易感的。</p><p>不过，15岁以下孩子、75岁以上老人患病比例较低。为了消除人口年龄结构的影响，我们做了人口年龄结构的加权，加权概率=该年龄段病例数/该年龄段占总人口的百分比点数，加权概率后仍然支持这个结论。</p><p><strong>传染时间呈U型，与春节假期有关</strong></p><div class="pgc-img"><img src="https://p3.pstatp.com/large/pgc-image/Rpixrmr3hGOALy" img_width="1788" img_height="1032" inline="0" alt="北师大经三千多病例发现，女性或无免于感染新型肺炎优势" onerror="javascript:errorimg.call(this);"><p class="pgc-img-caption"></p></div><p>图3 传染时间变化趋势图</p><p>通过计算分析3475个病例在1月18日到2月3日期间可能的传染时间（本文采用“传染时间=确诊时间-min（发病时间，就医时间）”计算方式，体现一个病例最重要的传染时间，包括病人及时就医或检查的确诊时间)，发现传染时间以1月26日为界，呈现出先下降再升高的趋势。</p><p>这一类似U型的变化趋势图或与春节前后全国范围内人员的大规模流动引起的病例数激增相关。</p><p>1月23日至1月26日，传染时间保持在4日左右的最低时间。这段时间正是腊月二十九到正月初二，全国人民基本都在自家过年，没有大范围的人员流动和聚集。</p><p><strong>病例越多，病患所造成的传染时间越长</strong></p><div class="pgc-img"><img src="https://p3.pstatp.com/large/pgc-image/RpixrndD5dKOIi" img_width="1796" img_height="820" inline="0" alt="北师大经三千多病例发现，女性或无免于感染新型肺炎优势" onerror="javascript:errorimg.call(this);"><p class="pgc-img-caption"></p></div><p>图4 （a）传染时间与省份总病例数间的相关性，（b）除广东、陕西外</p><p>通过将3475个病例按照所在省份进行统计分析，整理出病例数量大于60的12个省份。研究发现，不同省份之间的传染时间差异比较大，最快的山东四川接近3天，而比较慢的广东陕西达到6天以上。</p><p>进一步研究发现患者的传染时间与其所在省份总病例数之间存在较高的相关性，即省内感染者越多，传染时间越长。</p><p>在去掉较偏离武汉市的陕西省和广东省后，相关系数达到0.78。也就是说，病例越多，病患所造成的传染时间也就越长。这意味着病例增加的情况下，或因医院检测及诊治能力有限，耽搁了对疑似患者的诊断。</p><p>同时，这也意味着，感染者数量越多的省份，其他人被感染的风险也就越高。</p><p>广东省由于属于发达地区，医疗资源比较好，虽然病例数较多，但做到了及时发现，及时治疗，病例的传染时间相对短。</p><p><strong>典型省份确诊病例多有武汉短期经历，云南被动排查方式效果明显</strong></p><p>根据案例数和信息完整程度，我们选出浙江、安徽、云南和江苏四个典型省份，来观察四省病患的接触方式和发现方式。</p><div class="pgc-img"><img src="https://p3.pstatp.com/large/pgc-image/RpixtCzAEGvfIz" img_width="1398" img_height="1128" inline="0" alt="北师大经三千多病例发现，女性或无免于感染新型肺炎优势" onerror="javascript:errorimg.call(this);"><p class="pgc-img-caption"></p></div><div class="pgc-img"><img src="https://p3.pstatp.com/large/pgc-image/RpixtFkIbrwYm5" img_width="1478" img_height="1194" inline="0" alt="北师大经三千多病例发现，女性或无免于感染新型肺炎优势" onerror="javascript:errorimg.call(this);"><p class="pgc-img-caption"></p></div><p>图5 浙江、安徽、云南和江苏四省病患接触方式和发现方式</p><p>其中，浙江省作为一个疫情比较严重的省份，在接触方式里，银泰百货和TR188航班引起的病例值得关注，这都属于短时间的接触。其中被动隔离24个，被动排查11个，被动检查出来的比例还算偏低。</p><p>在安徽公开的信息中，从武汉回去的人员职业非常广泛，很多是小吃店、服装店等服务业从业人员，日常接触人员比较多，不论是感染风险还是传播风险都高于普通人员。</p><p>云南通过被动排查发现的患者比例较为突出，在卫生院、景点，甚至在收费站都有排查出患者。该省排查力度较高，相比之下，其他省份排查出来的比例少，还需要加大排查力度。</p><p>从统计来看，在四个省份的感染者中，有武汉短期经历的病例不少。他们大多是短期去武汉出差、旅游；其中有些只是为了在武汉转机、转车，或者去火车站接人，最短的甚至只有几个小时。</p><p>与确诊患者接触而引起的传播案例最高占30%以上。在确诊接触里，家人聚集/密切接触引发的病例最多；其次是聚餐引起。安徽聚餐引起的病例数达到19人次，符合新冠肺炎早期阶段传播的特点。</p><div class="pgc-img"><img src="https://p3.pstatp.com/large/pgc-image/RpixtGsAgBm2er" img_width="958" img_height="720" inline="0" alt="北师大经三千多病例发现，女性或无免于感染新型肺炎优势" onerror="javascript:errorimg.call(this);"><p class="pgc-img-caption"></p></div><p>图片来源：新京报网</p><p>此外，同感染者或潜在感染者乘车也有风险。因此，建议大家在乘坐公共交通工具时，尽量错峰而行，乘坐火车、飞机、地铁时，有条件的话，尽可能隔位而坐，或者分散而坐。</p><p>目前，新型冠状病毒缺乏有效的治疗方法，暂时也没有有效的疫苗，做好个人防护是最切实可行的方法。</p><p>从这次信息的收集来看，虽然目前各省市都能够对新冠肺炎的相关情况进行信息公开，但是不同地方其中收集和公开的信息的完整度往往差异很大。</p><p>因此建议相关部门能对相关数据及其格式标准化，地方疾控部门应该使用全国统一的上报系统，数据一经上报自动向全社会公开。疾病暴发初期，数据搜集的完整性和全面性对于早期判断传染病的流行病学关键参数性质非常重要。</p><p>□吴晔 李明会 樊佳（北京师范大学新闻传播学院）</p><p>参考资料：</p><p class="pgc-end-literature">1.《是否易感新型冠状病毒与年龄、性别有关系吗？》https：//new.qq.com/rain/a/20200201A0737E00</p><p class="pgc-end-literature">2.《&lt;柳叶刀&gt;刊文详解武汉肺炎最初41案例即有人传人迹象》http：//china.caixin.com/2020-01-26/101508499.html</p><p class="pgc-end-literature">3.《2019 新型冠状病毒疫情进展和风险评估》http：//www.chinacdc.cn/yyrdgz/202001/P020200128523354919292.pdf</p><p>数据来源：</p><p>各地卫健委网站、微博，感谢北京师范大学计算传播学研究中心的学生刘禹希，樊嘉，李小军，武臻，王琦，邓依林，王梦园，肖学怡，张晨锦，刘佳月的数据收集。</p><p>刘XX，男，**岁，装修工人。经常居留地为***，现住址为***。1月21日早上6：40左右，患者从汉口火车站（车次***，*车厢*号）出发，经湖北麻城火车站转车（车次**，*车厢*号下铺），中午11：00多到达**火车站。1月23日晚因发热乘出租车（期间戴口罩，出租车信息不详）到**市**医院感染病科就诊并隔离治疗。1月25日确诊为新型冠状病毒感染的肺炎。已查到的5名密切接触者正在医学观察，出租车司机及火车同乘人员正在查找。目前患者病情稳定。</p><p>编辑：李碧莹 校对：危卓</p><p>投稿、合作、联系我们：futurecity@xjbsmartcity.com</p>`,
            source: '新京报',
            publish_time: 1580993572
        
        }
    },
    created() {
        this.axios.get(`https://www.shuipingguo.com/news/?type=10`).then((response) => {
            this.data = response.data.data
        }).catch(() => {
        })
    }
}
</script>

